<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <nav class="bg-light p-0">
      <div class="container">
        <ul class="nav nav-pills nav-fill checkProgress">
          <li class="nav-item text-dark p-2">購物車清單</li>
          <li class="nav-item text-light p-2 active ">填寫訂單資訊</li>
          <li class="nav-item text-dark p-2">確認付款</li>
        </ul>
      </div>
    </nav>
    <div class="container">
      <div class="row p-3">
        <!-- 訂單 -->
        <div class="col-lg-8 col-12" >
          <h4 class="h4 mt-4">訂單資訊</h4>
          <hr>
            <form @submit.prevent="createOrder">
              <div class="form-group row">
                <div class="col">
                  <label for="username">收件人姓名</label>
                  <input type="text" class="form-control" name="name" id="username"
                  :class="{'is-invalid':errors.has('name')}"
                    v-model="orderForm.user.name" v-validate="'required'" placeholder="輸入姓名">
                  <span class="text-danger" v-if="errors.has('name')">收件人姓名是必填欄位</span>
                </div>
                <div class="col">
                  <label for="usertel">收件人電話</label>
                  <input type="tel" class="form-control" id="usertel"
                    v-model="orderForm.user.tel" placeholder="請輸入電話">
                </div>
              </div>
              <div class="form-group">
                <label for="useremail">Email</label>
                <input type="email" class="form-control" name="email" id="useremail"
                  v-model="orderForm.user.email" v-validate="'required|email'"
                  :class="{'is-invalid':errors.has('email')}" placeholder="請輸入 Email">
                <span class="text-danger" v-if="errors.has('email')">
                  {{errors.first('email')}}</span>
              </div>
              <div class="form-group">
                <label for="useraddress">收件人地址</label>
                <input type="address" class="form-control" name="address"
                  id="useraddress" v-model="orderForm.user.address" v-validate="'required'"
                  :class="{'is-invalid':errors.has('address')}" placeholder="請輸入地址">
                <span class="text-danger" v-if="errors.has('address')">地址欄位不得留空</span>
              </div>
              <div class="form-group">
                <label for="useraddress">備註</label>
                <textarea name="" id="" class="form-control"
                  cols="10" rows="6" v-model="orderForm.message"></textarea>
              </div>
              <div class="text-right">
                <button class="btn btn-danger">送出訂單</button>
              </div>
            </form>
          </div>
        <!-- 購物車清單 -->
        <div class="col-lg-4 col-12">
          <div class="bg-light p-3">
            <div class="h4 text-dark text-center p-2">購物車總覽</div>
            <div class="input-group mb-3 input-group-sm">
              <table class="table">
                <thead>
                </thead>
                <tbody>
                  <tr v-for="item in carts" :key="item.id">
                    <td></td>
                    <td class="align-middle">
                      {{ item.product.title }}
                      <div class="text-success" v-if="item.coupon">
                        已套用優惠券
                      </div>
                    </td>
                    <td class="align-middle">{{ item.qty }}{{ item.product.unit }}
                    </td>
                    <td class="align-middle">
                        NT$ {{ item.final_total }}
                    </td>
                  </tr>
                </tbody>
              <tfoot>
                <tr>
                    <td colspan="3" class="text-right">總計</td>
                    <td class="text-right">{{ totalPrice.total }}</td>
                  </tr>
                  <tr v-if="totalPrice.total !== totalPrice.final_total">
                    <td colspan="3" class="text-right text-success">折扣價</td>
                    <td class="text-right text-success">{{ totalPrice.final_total }}</td>
                  </tr>
                </tfoot>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import $ from 'jquery';

export default {
  data() {
    return {
      products: [],
      productDetail: {},
      status: {
        isLoadingItem: '',
      },
      isLoading: false,
      carts: [],
      totalPrice: [],
      coupon_code: '',
      orderForm: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: '',
        },
        message: '',
      },
    };
  },
  methods: {
    getProducts() {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOM}/products`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        vm.products = response.data.products;
        vm.isLoading = false;
      });
    },
    getProductDetail(id) {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/product/${id}`;
      const vm = this;
      vm.status.isLoadingItem = id;
      this.$http.get(url).then((response) => {
        vm.productDetail = response.data.product;
        $('#productModal').modal('show');
        vm.status.isLoadingItem = '';
      });
    },
    addToCart(id, qty = 1) {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/cart`;
      const vm = this;
      vm.status.isLoadingItem = id;
      const cart = {
        product_id: id,
        qty,
      };
      this.$http.post(url, { data: cart }).then((response) => {
        console.log(response.data);
        $('#productModal').modal('hide');
        vm.status.isLoadingItem = '';
        vm.getCartList();
      });
    },
    getCartList() {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/cart`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(url).then((response) => {
        vm.carts = response.data.data.carts;
        vm.totalPrice = response.data.data;
        vm.isLoading = false;
      });
    },
    removeCartItem(id) {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/cart/${id}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.delete(url).then(() => {
        vm.isLoading = false;
        vm.getCartList();
      });
    },
    addCouponCode() {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/coupon`;
      const vm = this;
      vm.isLoading = true;
      const coupon = {
        code: vm.coupon_code,
      };
      this.$http.post(url, { data: coupon }).then(() => {
        vm.isLoading = false;
        vm.getCartList();
      });
    },
    createOrder() {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/order`;
      const vm = this;
      const order = vm.orderForm;
      this.$validator.validate().then((result) => {
        if (result) {
          this.$http.post(url, { data: order }).then((response) => {
            console.log('訂單成立', response);
            if (response.data.success) {
              vm.$router.push(`/checkout/${response.data.orderId}`);
            }
            vm.getCartList();
          });
        }
      });
    },
  },
  created() {
    this.getProducts();
    this.getCartList();
  },
  computed: {
  },
};
</script>



