<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-md" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title">
              <span>{{productDetail.title}}</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-12">
                <div class="form-group" style="height: 400px; background-size: cover;
                  background-position: center center;"
                  :style="{backgroundImage:`url(${productDetail.imageUrl})`}">
                </div>
                <div class="card-body">
                  <div class="d-flex justify-content-between align-items-baseline">
                    <h5 class="card-title">{{productDetail.title}}</h5>
                    <span class="badge badge-secondary float-right ml-2">
                      {{productDetail.category}}</span>
                  </div>
                  <p class="card-text">{{productDetail.content}}</p>
                  <p class="card-text">{{productDetail.description}}</p>
                  <div class="d-flex justify-content-between align-items-baseline">
                    <div class="h5" v-if="!productDetail.price">
                      價格：{{productDetail.origin_price}}</div>
                    <del class="h6" v-if="productDetail.price">
                      原價：{{productDetail.origin_price}}</del>
                    <div class="h5" v-if="productDetail.price">
                      特惠價：{{productDetail.price}}</div>
                  </div>
                  <select class="form-control" v-model="productDetail.num">
                    <option :value="num" v-for="num in 10" :key="num">
                      {{num}} {{productDetail.unit}}</option>
                  </select>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <div class="h6">
                小計：{{productDetail.num * productDetail.price }} 元</div>
            <button type="button" class="btn btn-primary"
              @click="addToCart(productDetail.id, productDetail.num)">加到購物車</button>
          </div>
        </div>
      </div>
    </div>
    <!-- 商品列表 -->
    <div class="row mt-4">
      <div class="col-md-4 mt-4" v-for="item in products" :key="item.id">
        <div class="card border-0 shadow-sm">
          <div style="height: 300px; background-size: cover; background-position: center"
            :style="{backgroundImage:`url(${item.imageUrl})`}">
          </div>
          <div class="card-body">
            <span class="badge badge-secondary float-right ml-2">{{item.category}}</span>
            <h5 class="card-title">
              <a href="#" class="text-dark">{{item.title}}</a>
            </h5>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h5" v-if="!item.price">價格：{{item.origin_price}}</div>
              <del class="h6" v-if="item.price">原價：{{item.origin_price}}</del>
              <div class="h5" v-if="item.price">特惠價：{{item.price}}</div>
            </div>
          </div>
          <div class="card-footer d-flex">
            <button type="button" class="btn btn-outline-secondary btn-sm"
             @click="getProductDetail(item.id)">
              <i class="fas fa-spinner fa-spin" v-if="status.isLoadingItem === item.id"></i>
              查看更多
            </button>
            <button type="button" class="btn btn-outline-danger btn-sm ml-auto"
              @click="addToCart(item.id)">
              <i class="fas fa-spinner fa-spin" v-if="status.isLoadingItem === item.id"></i>
              加到購物車
            </button>
          </div>
        </div>
      </div>
    </div>
    <hr>
    <div class="row">
      <!-- 購物車清單 -->
      <div class="col-lg-6 col-12">
          <div class="h4 mt-4">購物車清單</div>
          <table class="table mt-4">
            <thead>
              <th></th>
              <th>品名</th>
              <th>數量</th>
              <th>單價</th>
            </thead>
            <tbody>
              <tr v-for="item in carts" :key="item.id">
                <td class="align-middle">
                  <button type="button" class="btn btn-outline-danger btn-sm"
                    @click="removeCartItem(item.id)">
                    <i class="far fa-trash-alt"></i>
                  </button>
                </td>
                <td class="align-middle">
                  {{ item.product.title }}
                  <div class="text-success" v-if="item.coupon">
                    已套用優惠券
                  </div>
                </td>
                <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                <td class="align-middle text-right">
                    {{ item.final_total }}
                  <small class="text-secondary text-left">
                    ( 節省{{ (item.product.origin_price * item.qty) - item.final_total }} )
                  </small>
                </td>
              </tr>
            </tbody>
          <tfoot>
            <tr>
                <td colspan="3" class="text-right">總計</td>
                <td class="text-right">{{ totalPrice.total }}</td>
              </tr>
              <tr v-if="totalPrice.total !== totalPrice.final_total">
                <td colspan="3" class="text-right text-success">折扣價</td>
                <td class="text-right text-success">{{ totalPrice.final_total }}</td>
              </tr>
            </tfoot>
          </table>
          <div class="input-group mb-3 input-group-sm">
            <input type="text" class="form-control" v-model="coupon_code" laceholder="請輸入優惠碼">
            <div class="input-group-append">
              <button class="btn btn-outline-secondary" type="button"
                @click="addCouponCode">
                套用優惠碼
              </button>
            </div>
          </div>
      </div>
      <hr>
      <!-- 訂單 -->
      <div class="col-lg-6 col-12" >
      <div class="h4 mt-4">建立清單</div>
        <form @submit.prevent="createOrder">
          <div class="form-group">
            <label for="useremail">Email</label>
            <input type="email" class="form-control" name="email" id="useremail"
              v-model="orderForm.user.email" v-validate="'required|email'"
              :class="{'is-invalid':errors.has('email')}" placeholder="請輸入 Email">
            <span class="text-danger" v-if="errors.has('email')">{{errors.first('email')}}</span>
          </div>
          <div class="form-group">
            <label for="username">收件人姓名</label>
            <input type="text" class="form-control" name="name" id="username"
            :class="{'is-invalid':errors.has('name')}"
              v-model="orderForm.user.name" v-validate="'required'" placeholder="輸入姓名">
            <span class="text-danger" v-if="errors.has('name')">收件人姓名是必填欄位</span>
          </div>
          <div class="form-group">
            <label for="usertel">收件人電話</label>
            <input type="tel" class="form-control" id="usertel"
              v-model="orderForm.user.tel" placeholder="請輸入電話">
          </div>
          <div class="form-group">
            <label for="useraddress">收件人地址</label>
            <input type="address" class="form-control" name="address"
              id="useraddress" v-model="orderForm.user.address" v-validate="'required'"
              :class="{'is-invalid':errors.has('address')}" placeholder="請輸入地址">
            <span class="text-danger" v-if="errors.has('address')">地址欄位不得留空</span>
          </div>
          <div class="form-group">
            <label for="useraddress">留言</label>
            <textarea name="" id="" class="form-control"
              cols="10" rows="6" v-model="orderForm.message"></textarea>
          </div>
          <div class="text-right">
            <button class="btn btn-danger">送出訂單</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import $ from 'jquery';

export default {
  data() {
    return {
      products: [],
      productDetail: {},
      status: {
        isLoadingItem: '',
      },
      isLoading: false,
      carts: [],
      totalPrice: [],
      coupon_code: '',
      orderForm: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: '',
        },
        message: '',
      },
    };
  },
  methods: {
    getProducts() {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOM}/products`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        vm.products = response.data.products;
        vm.isLoading = false;
      });
    },
    getProductDetail(id) {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/product/${id}`;
      const vm = this;
      vm.status.isLoadingItem = id;
      this.$http.get(url).then((response) => {
        vm.productDetail = response.data.product;
        $('#productModal').modal('show');
        vm.status.isLoadingItem = '';
      });
    },
    addToCart(id, qty = 1) {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/cart`;
      const vm = this;
      vm.status.isLoadingItem = id;
      const cart = {
        product_id: id,
        qty,
      };
      this.$http.post(url, { data: cart }).then((response) => {
        console.log(response.data);
        $('#productModal').modal('hide');
        vm.status.isLoadingItem = '';
        vm.getCartList();
      });
    },
    getCartList() {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/cart`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(url).then((response) => {
        vm.carts = response.data.data.carts;
        vm.totalPrice = response.data.data;
        vm.isLoading = false;
      });
    },
    removeCartItem(id) {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/cart/${id}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.delete(url).then(() => {
        vm.isLoading = false;
        vm.getCartList();
      });
    },
    addCouponCode() {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/coupon`;
      const vm = this;
      vm.isLoading = true;
      const coupon = {
        code: vm.coupon_code,
      };
      this.$http.post(url, { data: coupon }).then(() => {
        vm.isLoading = false;
        vm.getCartList();
      });
    },
    createOrder() {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/order`;
      const vm = this;
      const order = vm.orderForm;
      this.$validator.validate().then((result) => {
        if (result) {
          this.$http.post(url, { data: order }).then((response) => {
            console.log('訂單成立', response);
            if (response.data.success) {
              vm.$router.push(`/dashboard/customer-checkout/${response.data.orderId}`);
            }
            vm.getCartList();
          });
        }
      });
    },
  },
  created() {
    this.getProducts();
    this.getCartList();
  },
  computed: {
  },
};
</script>



