<template>
  <div>
    <div class="w-100 bg-xmas d-flex flex-column align-items-center
      justify-content-center p-4 text-style-special">
      <h3 class="text-light">Santa Claus Is Coming</h3>
      <h6 class="text-light">----- 25 Dec {{ countDay.years }} -----</h6>
      <div id="clock" class="clock d-flex ">
        <div class="d-flex flex-column align-items-center justify-content-center h3">
          <span id="days">{{countDay.days}}</span>
          <span class="h6">Days</span>
        </div>
        <div class="d-flex flex-column align-items-center justify-content-center h3">
          <span id="hours">{{countDay.hours}}</span>
          <span class="h6">Hours</span>
        </div>
        <div class="d-flex flex-column align-items-center justify-content-center h3">
          <span id="minutes">{{countDay.minutes}}</span>
          <span class="h6">Minutes</span>
        </div>
      </div>
    </div>
    <div class="container">
      <div class="row mt-4">
        <div class="col-md-3 mt-4">
          <div class="input-group mb-3">
            <input type="text" class="form-control" placeholder="搜尋"
            aria-label="搜尋" aria-describedby="basic-addon2" v-model="search">
            <div class="input-group-append">
              <button class="btn btn-darkGreen" type="button">
                <i class="fas fa-search"></i>
              </button>
            </div>
          </div>
          <div class="list-group category">
            <button type="button"
            class="list-group-item list-group-item-action
            d-flex justify-content-between align-items-center"
            style="cursor:pointer; outline:0"
            :class="{active:selected == 'all'}"
            @click="filterCategory('all')">全部
<!--             <span class="badge badge-leafGreen badge-pill">{{products.length}}</span> -->
            </button>
            <button type="button" v-for="item in categoryList" :key="item.category"
            class="list-group-item list-group-item-action
            d-flex justify-content-between align-items-center"
            style="cursor:pointer; outline:0"
            :class="{active:selected == item.category}"
            @click="filterCategory(`${item.category}`)">{{item.category}}
<!--             <span class="badge badge-leafGreen badge-pill">{{item.times}}</span> -->
            </button>
          </div>
        </div>
        <div class=" col-md-9">
          <div class="row">
            <div class="col-md-6 mt-4" v-for="item in filteredList" :key="item.id">
              <div class="card border-0 shadow">
                <div style="height: 300px; background-size: cover; background-position: center"
                  :style="{backgroundImage:`url(${item.imageUrl})`}">
                </div>
                <div class="card-body">
                  <span class="badge badge-secondary float-right ml-2">{{item.category}}</span>
                  <h5 class="card-title">
                    <a href="#" class="text-dark">{{item.title}}</a>
                  </h5>
                  <div class="d-flex justify-content-between align-items-baseline">
                    <div class="h5" v-if="!item.price">NT$ {{item.origin_price}}</div>
                    <div class="h5" v-if="item.price">NT$ {{item.price}}</div>
                    <del class="h6" v-if="item.price">原價：{{item.origin_price}}</del>
                  </div>
                </div>
              <div class="card-footer d-flex">
                <button type="button" class="btn btn-outline-leafGreen btn-sm mr-2 w-50"
                @click="getProductDetail(`${item.id}`)">
                  <i class="fas fa-spinner fa-spin" v-if="status.isLoadingItem === item.id"
                  ></i>
                  查看更多
                </button>
                <button type="button" class="btn btn-outline-xmas btn-sm ml-auto w-50"
                @click="addToCart(item.id)">
                  <i class="fas fa-spinner fa-spin" v-if="status.isLoadingItem === item.id"></i>
                  加到購物車
                </button>
              </div>
            </div>
            </div>
          </div>
          <Pagination class="mt-4" :page-data="pagination" @changePage="getProducts"></Pagination>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Pagination from './../Pagination';

export default {
  components: {
    Pagination,
  },
  data() {
    return {
      products: [],
      tempFilterProdunct: [],
      filterList: [],
      categoryList: [],
      selected: '',
      status: {
        isLoadingItem: '',
      },
      isLoading: false,
      search: '',
      countDown: {
        years: '',
        days: '',
        hours: '',
        minutes: '',
      },
      pagination: {},
    };
  },
  methods: {
    countDay() {
      const year = new Date().getFullYear();
      const thisYearDate = `Dec 25, ${year} 00:00:00`;
      const countDownDate = new Date(thisYearDate).getTime();
      const now = new Date().getTime();
      const distance = countDownDate - now;
      const vm = this;
      vm.countDay.years = year;
      vm.countDay.days = Math.floor(distance / (1000 * 60 * 60 * 24));
      vm.countDay.hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      vm.countDay.minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
      setInterval(vm.countDay, 20000);
    },
    getProducts(page = 1) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOM}/products?page=${page}`;
      const vm = this;
      vm.$store.dispatch('updateLoading', true);
      this.$http.get(api).then((response) => {
        vm.products = response.data.products;
        vm.$store.dispatch('updateLoading', false);
        vm.pagination = response.data.pagination;
        vm.filterCategory();
        vm.getfilterItem();
      });
    },
    getfilterItem() {
      function onlyUnique(value, index, self) {
        return self.indexOf(value) === index;
      }
      const vm = this;
      vm.filterList = [];
      for (let i = 0; i < vm.tempFilterProdunct.length; i += 1) {
        if (vm.tempFilterProdunct[i].category !== undefined) {
          vm.filterList.push(vm.tempFilterProdunct[i].category);
        }
      }
      const result = [];
      vm.filterList.forEach((item) => {
        result[item] = result[item] ? result[item] + 1 : 1;
      });
      vm.categoryList = [];
      vm.filterList.filter(onlyUnique).forEach((item) => {
        vm.categoryList.push({
          category: item,
          times: result[item],
        });
      });
    },
    filterCategory(category = 'all') {
      const vm = this;
      vm.tempFilterProdunct = [];
      vm.selected = category;
      vm.products.forEach((element) => {
        if (category === 'all') {
          vm.tempFilterProdunct = vm.products;
        } else if (element.category === category) {
          vm.tempFilterProdunct.push(element);
        }
      });
    },
    getProductDetail(id) {
      const vm = this;
      vm.$router.push(`/productDetail/${id}`);
    },
    getCartList() {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/cart`;
      const vm = this;
      this.$http.get(url).then((response) => {
        vm.carts = response.data.data.carts;
        this.$bus.$emit('cartNumber:push', vm.carts.length);
      });
    },
    addToCart(id, qty = 1) {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/cart`;
      const vm = this;
      vm.status.isLoadingItem = id;
      const cart = {
        product_id: id,
        qty,
      };
      this.$http.post(url, { data: cart }).then((response) => {
        console.log(response.data);
        vm.status.isLoadingItem = '';
        vm.getCartList();
      });
    },
  },
  computed: {
    filteredList() {
      return this.tempFilterProdunct.filter(post =>
        post.title.toLowerCase().includes(this.search.toLowerCase()));
    },
  },
  created() {
    this.getProducts();
    this.countDay();
  },
};
</script>
<style scoped>
.category .active {
  background: #155724;
  color: white;
  border-color: #155724;
}
.category button {
  border-color: #155724;
  border-style: dashed;
}
.clock div {
    margin: 10px;
    width: 100px;
}
.snowman {
  background-image:url(https://upload.cc/i1/2018/12/13/Z0loMr.png);
  background-repeat: no-repeat;
  background-size: 150px;
  background-position: left bottom;
}
</style>



