<template>
  <div>
    <loading :active.sync="isLoading">
      <template slot="default">
        <img src="http://5b0988e595225.cdn.sohucs.com/images/20171223/a9d87fd8360f4ac9a851e9ee9b3732c9.gif" width="300">
      </template>
    </loading>
    <nav class="navbar navbar-expand-lg navbar-light">
      <div class="container">
        <button class="navbar-toggler border-0" type="button" data-toggle="collapse"
        data-target="#navbarNav" aria-controls="navbarNav" style="outline:0;"
        aria-expanded="false" aria-label="Toggle navigation">
          <i class="fas fa-list-ul text-xmas" style="font-size: 32px;"></i>
        </button>
        <router-link class="nav-link" to="/home">
          <a class="d-flex align-items-center" href="#" style="text-decoration: none;">
            <img :src="logo" width="36" height="36">
            <span class="m-2 text-xmas h5 text-style-special">Santa's Gift</span>
          </a>
        </router-link>
        <div class="d-flex align-items-center order-lg-3">
          <div class="btn-group dropleft">
            <a class="dropdown-toggle position-relative" style="cursor:pointer;"
            @click="getCartList"
              data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              <img src="https://img.icons8.com/color/52/000000/christmas-stocking.png">
              <i class="fas fa-circle position-absolute count text-leafGreen"
                style="top:0; right:10px; font-size: 18px;">
                <small>{{cartsNumber}}</small>
              </i>
            </a>
            <div class="dropdown-menu" aria-labelledby="dropdownMenuLink"
             style="min-width:350px; max-height:200px; overflow-y: auto">
              <h6 class="h6 text-center">購物車清單
                <router-link  to="/orders">
                  <a href="#" class="badge badge-xmas" v-if="cartsNumber !== 0">我要結帳</a>
                </router-link>
              </h6>
              <h6 class="h6 text-center" v-if="cartsNumber == 0"><hr>購物車還是空的...</h6>
              <table class="table">
                <tbody>
                  <tr v-for="item in carts" :key="item.id">
                    <td class="align-middle">
                      <button type="button" class="btn btn-outline-danger btn-sm"
                        @click="removeCartItem(item.id)">
                        <i class="far fa-trash-alt"></i>
                      </button>
                    </td>
                    <td class="align-middle">
                      {{ item.product.title }}
                    </td>
                    <td class="align-middle">{{ item.qty }}{{ item.product.unit }}</td>
                    <td class="align-middle text-right">
                        NT${{ item.final_total }}
                    </td>
                  </tr>
                </tbody>
              </table>
              <hr>
              <p class="text-center m-0" style="font-size: 10px;">小提醒：前往結帳頁面可以再套用優惠卷哦！</p>
            </div>
          </div>
        </div>
        <div class="collapse navbar-collapse" id="navbarNav">
          <ul class="navbar-nav ml-3">
            <li class="nav-item active">
              <router-link class="nav-link" to="/home">
                <a class="nav-link text-dark font-weight-bold h6 m-0" href="#">
                  首頁<span class="sr-only">(current)</span></a>
              </router-link>
            </li>
            <li class="nav-item mx-lg-1">
              <router-link class="nav-link font-weight-bold h6 m-0" to="/market">
                <a class="nav-link text-dark" href="#">市集</a>
              </router-link>
            </li>
            <li class="nav-item mx-lg-1">
              <router-link class="nav-link font-weight-bold h6 m-0" to="/event">
                <a class="nav-link text-dark" href="#">活動</a>
              </router-link>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  </div>
</template>

<script>
import image from './../assets/santa-hat.png';

export default {
  data() {
    return {
      logo: image,
      carts: [],
      totalPrice: '',
      cartsNumber: '',
      show: false,
      isLoading: false,
    };
  },
  methods: {
    getCartList() {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/cart`;
      const vm = this;
      this.$http.get(url).then((response) => {
        vm.carts = response.data.data.carts;
        vm.totalPrice = response.data.data;
        vm.cartsNumber = vm.carts.length;
        console.log(vm.carts);
      });
    },
    removeCartItem(id) {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/cart/${id}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.delete(url).then(() => {
        vm.isLoading = false;
        vm.getCartList();
      });
    },
  },
  created() {
    this.getCartList();
    const vm = this;
    vm.$bus.$on('cartNumber:push', (cartsNumber) => {
      vm.cartsNumber = cartsNumber;
    });
  },
};
</script>
<style scoped>
.dropdown-toggle::before {
    display:none;
}
.count small {
  position: absolute;
  top: 2px;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  white-space: nowrap;
  font-size: 14px;
}
</style>



