<template>
  <div>
    <loading :active.sync="isLoading">
      <template slot="default">
        <img src="http://5b0988e595225.cdn.sohucs.com/images/20171223/a9d87fd8360f4ac9a851e9ee9b3732c9.gif" width="300">
      </template>
    </loading>
    <div
    style="height:600px;background-position: center center; background-size: cover;
    background-image:url(https://images.pexels.com/photos/1303085/pexels-photo-1303085.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940"
    class="d-flex align-items-center justify-content-center flex-column position-relative">
      <div id="snow"></div>
      <h1 class="text-light text-center text-style-special">
      Merry<br>Christmas<br><i class="fas fa-gift"></i></h1>
      <h4 class="h4 text-light text-center">挑個禮物，送給親愛的你</h4>
    </div>
    <div class="container">
      <div class="my-4 d-flex flex-column align-items-center justify-content-center">
        <h2 class="h2 my-3 text-xmas">精選商品</h2>
        <img :src="titleIcon" height="30">
      </div>
      <div class="swiper-container mt-4">
        <div class="swiper-wrapper">
          <div class="swiper-slide" style="cursor: pointer" v-for="item in products" :key="item.id" @click="getProductDetail(`${item.id}`)">
            <div class="card border-0 w-100">
              <div style="height: 300px; background-size: cover; background-position: center"
                :style="{backgroundImage:`url(${item.imageUrl})`}">
              </div>
              <div class="card-body bg-light">
                <h5 class="card-title">
                  <a href="#" class="text-dark">{{item.title}}</a>
                </h5>
                <h6 class="h6" v-if="!item.price">NT$ {{item.origin_price}}</h6>
                <h6 class="h6" v-if="item.price">
                    NT$ {{item.price}}
                    <small href="#" class="badge badge-xmas">限時特惠</small>
                </h6>
              </div>
              <button class="btn btn-leafGreen w-100 rounded-0
              text-style-special">View More</button>
            </div>
          </div>
        </div>
        <!-- Add Pagination -->
        <div class="swiper-pagination"></div>
      </div>
    </div>
    <div class="bg-light mt-4">
      <div class="container p-4">
        <div class="mb-5 d-flex flex-column align-items-center justify-content-center">
          <h2 class="h2 my-3 text-xmas">優惠活動</h2>
          <img :src="titleIcon" height="30">
        </div>
        <div class="row mx-auto pt-4">
          <div class="col-md-5 ml-auto"
          style="height: 300px; background-size: cover; background-position: center;
          background-image:url(https://images.pexels.com/photos/260485/pexels-photo-260485.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940);">
          </div>
          <div class="col-md-5 text-dark p-4 mr-auto
          d-flex flex-column align-items-center justify-content-center">
            <h4 class="h4 font-weight-bold mb-3 text-xmas"><i class="fas fa-bell "></i>
                聖誕優惠在這裡  <i class="fas fa-bell"></i></h4>
            <p class="h6">聖誕不剩單，單身也要送自己</p>
            <p class="text-left w-75">即日起至 12/25，點選『聖誕不剩單』，就可以領取全館七折折價卷，
              讓你聖誕節也不孤單，不送禮也能好便宜！
            </p>
            <router-link class="btn btn-leafGreen w-75" to="/event">
            聖誕不剩單</router-link>
          </div>
        </div>
        <div class="row pb-4">
          <div class="col-md-5 text-dark p-4 ml-auto order-md-1 order-2
          d-flex flex-column align-items-center justify-content-center">
            <h4 class="h4 font-weight-bold mb-3 text-xmas"><i class="fas fa-bell "></i>
              聖誕老公公換你做 <i class="fas fa-bell"></i></h4>
            <p class="h6">和神秘的他 / 她交換禮物吧！</p>
            <p class="text-left w-75">世紀首次與陌生人交換禮物，抽點選『我也要當聖誕老公公』，了解活動詳情並領取購物優惠卷，
              讓你好玩又好買，趕快來看看！！
            </p>
            <router-link class="btn btn-leafGreen w-75" to="/event">
              我也要當聖誕老公公</router-link>
          </div>
          <div class="col-md-5  mr-auto order-md-2"
          style="height: 300px; background-size: cover; background-position: center;
          background-image:url(https://images.pexels.com/photos/247891/pexels-photo-247891.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940);">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import icon from './../../assets/title.png';
/* global Swiper */
export default {
  data() {
    return {
      status: {
        isLoadingItem: '',
      },
      isLoading: false,
      products: [],
      titleIcon: icon,
    };
  },
  methods: {
    getProductDetail(id) {
      const vm = this;
      vm.$router.push(`/productDetail/${id}`);
    },
    getProducts() {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOM}/products`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        setTimeout(() => {
          const swiper = new Swiper('.swiper-container', {
            loop: true,
            autoplay: {
              delay: 2000,
            },
            slidesPerView: 3,
            spaceBetween: 20,
            breakpoints: {
              1024: {
                slidesPerView: 3,
                spaceBetween: 30,
              },
              960: {
                slidesPerView: 2,
                spaceBetween: 30,
              },
              640: {
                slidesPerView: 1,
                spaceBetween: 20,
              },
            },
          });
          console.log(swiper);
        }, 100);
        console.log(response.data);
        vm.products = response.data.products;
        vm.isLoading = false;
      });
    },
  },
  created() {
    this.getProducts();
  },
};
</script>
<style scoped>
#snow {
  background-image: url(https://upload.cc/i1/2018/12/07/Wfyb7M.png),
  url(https://upload.cc/i1/2018/12/07/ujpLfe.png),
  url(https://upload.cc/i1/2018/12/07/eNTXlE.png);
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: inherit;
  animation: snow 10s linear infinite;
}
@keyframes snow {
  0% {
    background-position: 0px 0px, 0px 0px, 0px 0px;
  }
  50% {
    background-position: 500px 500px, 100px 200px, -100px 150px;
  }
  100% {
    background-position: 500px 1000px, 200px 400px, -100px 300px;
  }
}
.swiper-container {
  width: 100%;
  height: 100%;
}
.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  /* Center slide text vertically */
  display: flex;
}
</style>



