<template>
  <div>
    <loading :active.sync="isLoading">
      <template slot="default">
        <img src="http://5b0988e595225.cdn.sohucs.com/images/20171223/a9d87fd8360f4ac9a851e9ee9b3732c9.gif" width="300">
      </template>
    </loading>
    <Header/>
    <router-view></router-view>
    <Footer/>
  </div>
</template>
<script>
import Header from './Header';
import Footer from './Footer';

export default {
  components: {
    Header,
    Footer,
  },
  computed: {
    isLoading() {
      return this.$store.state.isLoading;
    },
  },
};
</script>



