<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right mt-4">
      <button class="btn btn-primary" @click="openCouponModal(true)">建立優惠卷</button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="120">標題</th>
          <th width="60">數量</th>
          <th width="80">折扣（%）</th>
          <th width="160">到期日</th>
          <th width="160">優惠碼</th>
          <th width="80">編輯</th>
        </tr>
      </thead>
      <tbody >
        <tr v-for="item in coupons" :key="item.id">
          <td>{{item.title}}</td>
          <td>{{item.is_enabled}}</td>
          <td>{{item.percent}}</td>
          <td>{{item.due_date}}</td>
          <td>{{item.code}}</td>
          <td>
            <button class="btn btn-outline-primary btn-sm"
              @click="openCouponModal(false, false, item)">編輯</button>
            <button class="btn btn-outline-danger btn-sm ml-1"
              @click="openCouponModal(false, true, item)">刪除</button>
          </td>
        </tr>
      </tbody>
    </table>
    <Pagination :page-data="pagination" @changePage="getCoupons"></Pagination>
    <!-- Modal -->
    <div class="modal fade" id="couponModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-md" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>新增優惠卷</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label for="title">標題</label>
              <input type="text" class="form-control" id="title"
                v-model="tempCoupon.title" placeholder="請輸入標題">
            </div>
            <div class="form-row">
              <div class="form-group col-md-6">
                <label for="is_enabled">數量</label>
                <input type="number" class="form-control" id="is_enabled"
                  v-model="tempCoupon.is_enabled" placeholder="請輸入數量">
              </div>
              <div class="form-group col-md-6">
                <label for="percent">折扣（%）</label>
                <input type="text" class="form-control" id="percent"
                  v-model="tempCoupon.percent" placeholder="請輸入折扣（%）">
              </div>
            </div>
            <div class="form-row">
              <div class="form-group col-md-6">
              <label for="due_date">到期日</label>
                <input type="date" class="form-control" id="due_date"
                  v-model="tempCoupon.due_date" placeholder="請輸入到期日">
              </div>
              <div class="form-group col-md-6">
                <label for="code">優惠碼</label>
                <input type="text" class="form-control" id="code"
                  v-model="tempCoupon.code" placeholder="請輸入優惠碼">
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-primary" @click="updateCoupon">確認</button>
          </div>
        </div>
      </div>
    </div>
    <!-- <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>刪除產品</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            是否刪除 <strong class="text-danger">{{ tempProduct.title }}</strong> 商品(刪除後將無法恢復)。
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-danger" @click="updateProduct"
              >確認刪除</button>
          </div>
        </div>
      </div>
    </div> -->
  </div>
</template>
<script>
import $ from 'jquery';
import Pagination from './../Pagination';

export default {
  components: {
    Pagination,
  },
  data() {
    return {
      coupons: [],
      tempCoupon: {},
      isNew: false,
      isLoading: false,
      del: false,
      pagination: {},
    };
  },
  methods: {
    getCoupons(page = 1) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOM}/admin/coupons?page=${page}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        console.log(response.data.coupons);
        vm.coupons = response.data.coupons;
        vm.isLoading = false;
        vm.pagination = response.data.pagination;
      });
    },
    openCouponModal(isNew, del, item) {
      if (del === true) {
        $('#couponModal').modal('show');
        this.tempCoupon = Object.assign({}, item);
        this.isNew = false;
        this.del = true;
      } else {
        $('#couponModal').modal('show');
        if (isNew) {
          this.tempCoupon = {};
          this.del = false;
          this.isNew = true;
        } else {
          this.tempCoupon = Object.assign({}, item);
          this.del = false;
          this.isNew = false;
        }
      }
    },
    updateCoupon() {
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOM}/admin/coupon`;
      let httpMethod = 'post';
      const vm = this;
      if (!vm.isNew && !vm.del) {
        api = `${process.env.APIPATH}/api/${process.env.CUSTOM}/admin/coupon/${vm.tempCoupon.id}`;
        httpMethod = 'put';
      }
      // if (!vm.isNew && vm.del) {
      //   api = `${process.env.APIPATH}/api/
      // ${process.env.CUSTOM}/admin/product/${vm.tempCoupon.id}`;
      //   httpMethod = 'delete';
      // }
      this.$http[httpMethod](api, { data: vm.tempCoupon }).then((response) => {
        console.log(response.data.success);
        if (response.data.success) {
          $('#couponModal').modal('hide');
          // $('#delProductModal').modal('hide');
          vm.getCoupons();
        } else {
          // $('#delProductModal').modal('hide');
          $('#couponModal').modal('hide');
          vm.getCoupons();
          this.$bus.$emit('message:push', '執行失敗', 'danger');
        }
      });
    },
  },
  created() {
    this.getCoupons();
  },
};
</script>



