<template>
  <div>
    <div class="container mt-3">
      <nav aria-label="breadcrumb">
        <ol class="breadcrumb bg-leafGreen text-light">
          <li class="breadcrumb-item">
            <router-link class="text-light" to="/market/">市集</router-link>
          </li>
          <li class="breadcrumb-item text-light
          active" aria-current="page">{{productDetail.category}}</li>
        </ol>
      </nav>
      <div class="row pt-4">
        <div class="col-md-6">
          <div style="height: 500px; background-size: cover; background-position: center;
          border: 20px solid #f5f5f5"
            :style="{backgroundImage:`url(${productDetail.imageUrl})`}">
          </div>
        </div>
        <div class="col-md-6 d-flex flex-column d-flex justify-content-between p-3">
          <div>
            <div class="d-flex flex-row">
              <h2 class="h2">
                <a href="#" class="text-dark">{{productDetail.title}}</a>
              </h2>
              <h6><span class="badge badge-leafGreen m-1"
                v-if="productDetail.category">
                {{productDetail.category}}</span></h6>
            </div>
            <hr>
            <div>
              <h6 class="h6 mb-4">{{productDetail.description}}</h6>
            </div>
          </div>
          <div>
            <hr>
            <h6 class="h6 mb-4" v-if="productDetail.content">商品備註：<br>{{productDetail.content}}</h6>
            <div class="d-flex justify-content-between align-items-end mt-2 p-1">
              <p class="h5" v-if="!productDetail.price">價格：{{productDetail.origin_price}}</p>
              <p class="h4 font-weight-bold" v-if="productDetail.price">NT$ {{productDetail.price}}
                <span class="text-muted h6">
                  ( 節省了 NT$ {{productDetail.origin_price - productDetail.price}})
                </span>
              </p>
              <p class="h6 discount text-muted mx-2" v-if="productDetail.price">
                原價：NT$ {{productDetail.origin_price}}</p>
            </div>
            <select class="form-control mr-2" v-model="productDetail.num">
              <option :value="num" v-for="num in 10" :key="num">
                {{num}} {{productDetail.unit}}</option>
            </select>
            <button type="button" class="btn btn-leafGreen mt-2 w-100"
            @click="addToCart(productDetail.id, productDetail.num)">加到購物車</button>
          </div>
        </div>
        </div>
    </div>
  </div>
</template>
<script>

export default {
  data() {
    return {
      productId: '',
      productDetail: {},
      status: {
        isLoadingItem: '',
      },
      carts: [],
      totalPrice: [],
      isLoading: false,
    };
  },
  methods: {
    getOrderDetail() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/product/${vm.productId}`;
      vm.$store.dispatch('updateLoading', true);
      this.$http.get(url).then((response) => {
        console.log(response.data.product);
        vm.productDetail = response.data.product;
        vm.$store.dispatch('updateLoading', false);
      });
    },
    getCartList() {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/cart`;
      const vm = this;
      this.$http.get(url).then((response) => {
        vm.carts = response.data.data.carts;
        vm.totalPrice = response.data.data;
        this.$bus.$emit('cartNumber:push', vm.carts.length);
      });
    },
    addToCart(id, qty = 1) {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/cart`;
      const vm = this;
      vm.status.isLoadingItem = id;
      const cart = {
        product_id: id,
        qty,
      };
      this.$http.post(url, { data: cart }).then((response) => {
        console.log(response.data);
        vm.status.isLoadingItem = '';
        vm.getCartList();
      });
    },
  },
  created() {
    this.productId = this.$route.params.orderId;
    this.getOrderDetail();
  },
};
</script>
<style scoped>
.breadcrumb-item + .breadcrumb-item::before {
 color: #f5f5f5 !important;
}
</style>



