import Vue from 'vue';
import Router from 'vue-router';
import Login from '@/components/pages/Login';
import Layout from '@/components/Layout';
import Home from '@/components/front/Home';
import Market from '@/components/front/Market';
import Orders from '@/components/front/Orders';
import XmasEvent from '@/components/front/XmasEvent';
import Check from '@/components/front/Check';
import CheckOut from '@/components/front/CheckOut';
import productDetail from '@/components/front/productDetail';
import Dashboard from '@/components/Dashboard';
import Products from '@/components/pages/Product';
import CustomerOrders from '@/components/pages/CustomerOrders';
import OrderList from '@/components/pages/OrderList';
import addCoupon from '@/components/pages/addCoupon';
import CustomerCheckout from '@/components/pages/CustomerCheckout';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '*',
      redirect: 'login',
    },
    {
      path: '/',
      redirect: '/home',
      name: 'Layout',
      component: Layout,
      children: [
        {
          path: '/home',
          name: 'Home',
          component: Home,
        },
        {
          path: 'market',
          name: 'Market',
          component: Market,
        },
        {
          path: 'event',
          name: 'XmasEvent',
          component: XmasEvent,
        },
        {
          path: 'productDetail/:orderId',
          name: 'productDetail',
          component: productDetail,
        },
        {
          path: 'orders',
          name: 'Orders',
          component: Orders,
        },
        {
          path: 'check',
          name: 'Check',
          component: Check,
        },
        {
          path: 'checkout/:orderId',
          name: 'CheckOut',
          component: CheckOut,
        },
      ],
    },
    {
      path: '/admin',
      name: 'Dashboard',
      component: Dashboard,
      children: [
        {
          path: 'orderlist',
          name: 'OrderList',
          component: OrderList,
        },
        {
          path: 'products',
          name: 'Products',
          component: Products,
          meta: { requiresAuth: true },
        },
        {
          path: 'addCoupon',
          name: 'addCoupon',
          component: addCoupon,
          meta: { requiresAuth: true },
        },
      ],
    },
    {
      path: '/dashboard',
      name: 'Dashboard',
      component: Dashboard,
      children: [
        {
          path: 'customer-orders',
          name: 'CustomerOrders',
          component: CustomerOrders,
        },
        {
          path: 'customer-checkout/:orderId',
          name: 'CustomerCheckout',
          component: CustomerCheckout,
        },
      ],
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
    },
  ],
});



