<template>
  <div>
    <nav class="bg-light p-0">
      <div class="container">
        <ul class="nav nav-pills nav-fill checkProgress">
          <li class="nav-item text-dark p-2">購物車清單</li>
          <li class="nav-item text-dark p-2">填寫訂單資訊</li>
          <li class="nav-item text-light p-2 active">確認付款</li>
        </ul>
      </div>
    </nav>
    <div class="container">
      <div class="row p-3">
        <div class="col-lg-8 col-12" >
          <h4 class="h4 mt-4">確認付款資訊</h4>
          <div class="my-5 row justify-content-center">
            <form class="col-12" @submit.prevent="payOrder">
              <table class="table">
                <tbody>
                  <tr>
                    <th width="100">Email</th>
                    <td>{{ orderDetail.user.email }}</td>
                  </tr>
                  <tr>
                    <th>姓名</th>
                    <td>{{ orderDetail.user.name }}</td>
                  </tr>
                  <tr>
                    <th>收件人電話</th>
                    <td>{{ orderDetail.user.tel }}</td>
                  </tr>
                  <tr>
                    <th>收件人地址</th>
                    <td>{{ orderDetail.user.address }}</td>
                  </tr>
                  <tr>
                    <th>付款狀態</th>
                    <td>
                      <span v-if="!orderDetail.is_paid">尚未付款</span>
                      <span v-else class="text-success">付款完成</span>
                    </td>
                  </tr>
                </tbody>
              </table>
              <div class="text-right" v-if="orderDetail.is_paid === false">
                <button class="btn btn-danger">確認付款去</button>
              </div>
            </form>
          </div>
        </div>
        <!-- 購物車清單 -->
        <div class="col-lg-4 col-12">
          <div class="bg-light p-3">
            <div class="h4 text-dark text-center p-2">購物車總覽</div>
            <table class="table">
              <thead>
                <th>品名</th>
                <th>數量</th>
                <th>單價</th>
              </thead>
              <tbody>
                <tr v-for="item in orderDetail.products" :key="item.id">
                  <td class="align-middle">{{ item.product.title }}</td>
                  <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                  <td class="align-middle text-right">{{ item.final_total }}</td>
                </tr>
              </tbody>
              <tfoot>
                <tr>
                  <td colspan="2" class="text-right">總計</td>
                  <td class="text-right">{{ orderDetail.total }}</td>
                </tr>
              </tfoot>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      orderId: '',
      orderDetail: {
        user: {},
      },
    };
  },
  methods: {
    getOrderDetail() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/order/${vm.orderId}`;
      vm.isLoading = true;
      this.$http.get(url).then((response) => {
        console.log(response);
        vm.orderDetail = response.data.order;
        vm.isLoading = false;
      });
    },
    payOrder() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/pay/${vm.orderId}`;
      vm.isLoading = true;
      this.$http.post(url).then((response) => {
        console.log(response);
        if (response.data.success) {
          this.getOrderDetail();
          setTimeout(() => this.$router.push({ path: '/home' }), 3000);
        }
        vm.isLoading = false;
      });
    },
  },
  created() {
    this.orderId = this.$route.params.orderId;
    this.getOrderDetail();
  },
};
</script>



