import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

// 把 store Export 出來
export default new Vuex.Store({
  state: {
    isLoading: false,
  },
  actions: {
    updateLoading(context, status) {
      context.commit('LOADING', status);
    },
  },
  mutations: { // 改變資料狀態
    // 這邊的命名建議大寫
    LOADING(state, status) {
      state.isLoading = status;
    },
  },
});



