<template>
  <div>
    <nav class="bg-light p-0">
      <div class="container">
        <ul class="nav nav-pills nav-fill checkProgress">
          <li class="nav-item active text-light p-2">購物車清單</li>
          <li class="nav-item text-dark p-2">填寫訂單資訊</li>
          <li class="nav-item text-dark p-2">確認付款</li>
        </ul>
      </div>
    </nav>
    <div class="container mt-5">
      <div class="row p-3">
        <!-- 訂單 -->
        <div class="col-lg-7 col-12 mr-md-5" >
          <h3 class="text-style-special my-4">Shopping Cart</h3>
          <table class="table mt-4">
            <thead>
              <th class="d-none d-md-block border-0">商品圖</th>
              <th class="border-0">品名</th>
              <th class="border-0">數量</th>
              <th class="border-0">單價</th>
              <th class="border-0">總價</th>
              <th class="border-0"></th>
            </thead>
            <tbody>
              <tr v-for="item in carts" :key="item.id">
                <td class="align-middle d-none d-lg-block">
                  <img :src="item.product.imageUrl" alt="" width="120">
                </td>
                <td class="align-middle">
                  {{ item.product.title }}
                </td>
                <td class="align-middle">{{ item.qty }}{{ item.product.unit }}</td>
                <td class="align-middle">NT$ {{ item.product.price }}</td>
                <td class="align-middle">
                    NT$ {{ item.final_total }}
                  <small class="text-secondary text-left d-none d-lg-block">
                    ( 節省{{ (item.product.origin_price * item.qty) - item.final_total }} )
                  </small>
                </td>
                <td class="align-middle">
                  <button type="button" class="btn btn-outline-danger btn-sm"
                    @click="removeCartItem(item.id)">
                    <i class="far fa-trash-alt"></i>
                  </button>
                </td>
              </tr>
          </tbody>
          </table>
          <hr>
        </div>
        <!-- 購物車清單 -->
        <div class="d-flex flex-column col-lg-4 col-12 px-0">
          <div class="bg-light p-3">
            <div class="h4 text-dark text-center p-2">購物車總覽</div>
            <div class="input-group mb-3 input-group-sm">
              <table class="table">
                <thead>
                </thead>
                <tbody>
                  <tr v-for="item in carts" :key="item.id">
                    <td></td>
                    <td class="align-middle">
                      {{ item.product.title }}
                      <div class="text-success" v-if="item.coupon">
                        已套用優惠券
                      </div>
                    </td>
                    <td class="align-middle">{{ item.qty }}{{ item.product.unit }}
                    </td>
                    <td class="align-middle">
                        NT$ {{ item.final_total }}
                    </td>
                  </tr>
                </tbody>
              <tfoot>
                <tr>
                    <td colspan="3" class="text-right">總計</td>
                    <td class="text-right">{{ totalPrice.total }}</td>
                  </tr>
                  <tr v-if="totalPrice.total !== totalPrice.final_total">
                    <td colspan="3" class="text-right text-success">折扣價</td>
                    <td class="text-right text-success">{{ totalPrice.final_total }}</td>
                  </tr>
                </tfoot>
              </table>
              <div class="input-group mb-3">
                <input type="text" class="form-control" v-model="coupon_code" laceholder="請輸入優惠碼">
                <div class="input-group-append">
                  <button class="btn btn-sm btn-outline-danger" type="button"
                  @click="addCouponCode">
                  套用優惠碼
                  </button>
                </div>
              </div>
            </div>
          </div>
          <div class="row px-3">
            <router-link class="col-md-6 btn btn-md
            btn-leafGreen rounded-0" to="/market">回市集</router-link>
            <router-link class="col-md-6 btn btn-md
            btn-danger rounded-0" to="/check">我要結帳</router-link>
          </div>
        </div>
        <hr>
      </div>
    </div>
  </div>
</template>
<script>
import $ from 'jquery';

export default {
  data() {
    return {
      products: [],
      productDetail: {},
      status: {
        isLoadingItem: '',
      },
      isLoading: false,
      carts: [],
      totalPrice: [],
      coupon_code: '',
      orderForm: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: '',
        },
        message: '',
      },
    };
  },
  methods: {
    getProducts() {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOM}/products`;
      const vm = this;
      vm.$store.dispatch('updateLoading', true);
      this.$http.get(api).then((response) => {
        vm.products = response.data.products;
        vm.$store.dispatch('updateLoading', false);
      });
    },
    getProductDetail(id) {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/product/${id}`;
      const vm = this;
      vm.status.isLoadingItem = id;
      this.$http.get(url).then((response) => {
        vm.productDetail = response.data.product;
        $('#productModal').modal('show');
        vm.status.isLoadingItem = '';
      });
    },
    addToCart(id, qty = 1) {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/cart`;
      const vm = this;
      vm.status.isLoadingItem = id;
      const cart = {
        product_id: id,
        qty,
      };
      this.$http.post(url, { data: cart }).then((response) => {
        console.log(response.data);
        $('#productModal').modal('hide');
        vm.status.isLoadingItem = '';
        vm.getCartList();
      });
    },
    getCartList() {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/cart`;
      const vm = this;
      vm.$store.dispatch('updateLoading', true);
      this.$http.get(url).then((response) => {
        vm.carts = response.data.data.carts;
        vm.totalPrice = response.data.data;
        vm.$store.dispatch('updateLoading', false);
      });
    },
    removeCartItem(id) {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/cart/${id}`;
      const vm = this;
      vm.$store.dispatch('updateLoading', true);
      this.$http.delete(url).then(() => {
        vm.$store.dispatch('updateLoading', false);
        vm.getCartList();
      });
    },
    addCouponCode() {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/coupon`;
      const vm = this;
      vm.$store.dispatch('updateLoading', true);
      const coupon = {
        code: vm.coupon_code,
      };
      this.$http.post(url, { data: coupon }).then(() => {
        vm.$store.dispatch('updateLoading', false);
        vm.getCartList();
      });
    },
    createOrder() {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOM}/order`;
      const vm = this;
      const order = vm.orderForm;
      this.$validator.validate().then((result) => {
        if (result) {
          this.$http.post(url, { data: order }).then((response) => {
            console.log('訂單成立', response);
            if (response.data.success) {
              vm.$router.push(`/checkout/${response.data.orderId}`);
            }
            vm.getCartList();
          });
        }
      });
    },
  },
  created() {
    this.getProducts();
    this.getCartList();
  },
  computed: {
  },
};
</script>



