        <div class="d-flex justify-content-start align-items-md-center
        py-4 pl-4 my-4 event event1 col-md-10 mx-auto">
          <div class="d-flex flex-column text-center  col-md-5">
            <h2 class="text-xmas py-2">聖誕不剩單</h2>
            <div class="font-weight-normal h5">全館七折折價卷</div>
            <h2 class="h2 text-style-special p-4">xmasSingleOut</h2>
            <small>結帳時輸入代碼就可以享有優惠囉</small>
          </div>
        </div>
        <div class="d-flex justify-content-end align-items-md-center
        py-5  my-4 event event2 col-md-10 mx-auto">
          <div class="d-flex flex-column text-center">
            <h3 class="text-xmas py-2">聖誕老公公換你做 </h3>
            <div class="font-weight-normal h6">和神秘的他 / 她交換禮物吧！</div>
            <p class="font-weight-normal mx-auto p-3 w-75 text-left">只要購買禮物並登記你要參加此活動，報名成功後就會得到一組序號。
              <br>
