<script>
import icon from './../../assets/title.png';

export default {
  data() {
    return {
      titleIcon: icon,
    };
  },
  created() {
    document.documentElement.scrollTop = 0;
  },
};
</script>
<style scoped>
.event {
  background-position: center left;
  background-size: cover;
  min-height: 420px;
}
.event1,.event2 {
  background-image:url(https://images.unsplash.com/photo-1508161250369-a7ecbdfb67fe?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1500&q=80);
}
@media (min-width:780px) {
  .event1 {
    background-image:url(https://images.unsplash.com/photo-1512284386138-e4afa2348a9b?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=668&q=80);
  }
  .event2 {
    background-image:url(https://images.unsplash.com/photo-1510162126413-f644071b8f60?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1534&q=80);
  }
}
</style>



